import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { formatGram, localStoragedecryp } from "../../../../components/helpers/function.jsx";
import "react-datepicker/dist/react-datepicker.css";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification.jsx";
// import CetakLaporanDetailBarang from "./CetakLaporanDetailBarang.jsx";
// import FormFilter from "./FormFilter.jsx";
import { getDataNoTimeOut } from "../../../../config/axios.jsx";
import HeadFilterLaporan from "./HeadFilterLaporan.jsx";
// import LaporanDetailexel from "./LaporanDetailExel.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormDetailBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      export: false,
      kode_baki: [],
      LaporanBarangDetail: false,
      columns: [
        // {
        //   dataField: "kode_barang",
        //   text: "Kode Barang ",
        //   sort: true,
        //   footerAttrs: {
        //     colSpan: "5",
        //   },
        //   footer: (cell, row, num, index) => {
        //     return (
        //       <div>Total Barang : {this.state.LaporanBarangDetail.length || 0} </div>
        //     );
        //   },
        // },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "6",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>Total Barang : {this.state.LaporanBarangDetail.length || 0} </div>
            );
          },
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "kode_gudang",
          text: "Kode Gudang",
        },
        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
          footer: (columnData) => {
            return <div className="text-right"> {formatGram(columnData.reduce((acc, item) => acc + item, 0), 3)} </div>
          }
        },
        // {
        //   dataField: "stock_on_hand",
        //   text: "Jumlah",
        //   formatter: (cell) => {
        //     return cell;
        //   },
        //   footer: (columnData) =>
        //     columnData.reduce((acc, item) => acc + item, 0),
        // },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true, kode_toko: data.kode_baki });
    // if (data.kode_baki === undefined) {
    //   Swal.fire({
    //     // title: 'Good Job',
    //     text: "Kode Baki Tidak Boleh Kosong",
    //     icon: "info",
    //     position: "top-center",
    //     timer: 2000,
    //     width: "300px",
    //     height: "100px",
    //     confirmButtonText: "OK",
    //     showConfirmButton: true,
    //   });
    //   //   this.props.dispatch(reset("FormFilter"));
    //   this.setState({ isLoading: false });
    //   return false;
    // }
    //   console.log(data_post);

    getDataNoTimeOut(`reportbarang/barangdetail?kode_gudang=${data.kode_gudang || "ALL"}&kode_toko=${data.kode_baki || "ALL"}&kode_jenis=${data.kode_jenis || "ALL"}&kode_group=${data.kode_group || "ALL"}`)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
          });
          ToastNotification("info", "Laporan Barang Detail Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Barang Detail Tersedia");
          this.setState({
            export: true,
            kode_baki: data.kode_baki,
            LaporanBarangDetail: res.data,
          });
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }
  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoading: false });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Barang Detail</li>
        </ol>
        <h1 className="page-header">Laporan Barang Detail</h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFilterLaporan
                  kode_baki={this.state.kode_baki}
                  export={this.state.export}
                  columns={this.state.columns}
                  data={this.state.LaporanBarangDetail}
                  isLoading={this.state.isLoading}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
        
            </div>
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormDetailBarang = reduxForm({
  form: "FormDetailBarang",
  enableReinitialize: true,
})(FormDetailBarang);
export default connect(maptostate, null)(FormDetailBarang);
