import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  ReanderSelect,
  SkeletonLoading,
} from "../../../../components/helpers/field";
import { getDataBaki, getDataBakiByGudang, GetDataGroup, getDataGudang, getDataJenis } from "../../../../actions/datamaster_action";
import CetakLaporanDetailBarang from "./pdf/CetakLaporanDetailBarang";
import LaporanDetailBarangExel from "./exel/LaporanDetailBarangExel";
import Tabel from "../../../../components/helpers/tabel";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    dataJenis : state.datamaster.getDataJenis,
    dataGroup : state.datamaster.GetDataGroup,
    dataGudang : state.datamaster.getDataGudang
  };
};
class HeadFilterLaporan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      list_baki: [
        {
          value : "SEMUA",
          name : "SEMUA"
        }
      ]
    };
  }

  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getDataBaki());
    this.props.dispatch(getDataGudang());

    setTimeout(() => {
        this.props.change("kode_group","ALL")
        this.props.change("kode_jenis","ALL")
        this.props.change("kode_gudang","ALL")
        this.props.change("kode_baki","ALL")
    }, 100);
  }
  exportPdf() {
    CetakLaporanDetailBarang(this.props.data, this.props.kode_baki);
  }
  render() {
    // console.log(this.props.dataJenis);
    let databaki =  [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ];
    this.props.databaki.forEach(list => {
      let data = {
        value: list.kode_baki,
        name: list.kode_baki + " - " + list.nama_baki,
      };
      databaki.push(data)
    });

    let dataGroup =  [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ];
    this.props.dataGroup.forEach(list => {
      let data = {
        value: list.kode_group,
        name: list.kode_group + " - " + list.nama_group,
      };
      dataGroup.push(data)
    });

    let dataJenis =  [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ];
    this.props.dataJenis.forEach(list => {
      let data = {
        value: list.kode_dept,
        name: list.kode_dept + " - " + list.nama_dept,
      };
      dataJenis.push(data)
    });

    let dataGudang =  [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ];
    this.props.dataGudang.forEach(list => {
      let data = {
        value: list.kode_gudang,
        name: list.kode_gudang + " - " + list.nama_gudang,
      };
      dataGudang.push(data)
    });
    return (
      <>
        <form
          onSubmit={(data) => this.props.handleSubmit(data)}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
          <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Group"
                  name="kode_group"
                  options={dataGroup}
                  placeholder={"Silahkan Pilih Kode Group"}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Group" />
              )}
            </div>
            <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Jenis"
                  name="kode_jenis"
                  options={dataJenis}
                  placeholder={"Silahkan Pilih Kode Jenis"}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Jenis" />
              )}
            </div>
            <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Gudang"
                  name="kode_gudang"
                  options={dataGudang}
                  placeholder={"Silahkan Pilih Kode Gudang"}
                  onChange={(e) => {
                      if(e !== "ALL"){
                        this.props.dispatch(getDataBakiByGudang(e))
                      } else {
                        this.props.dispatch(getDataBaki())
                      }
                  }}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Gudang" />
              )}
            </div>
            <div className="col-3">
              {this.state.loading === false ? (
                <Field
                  label="Kode Baki"
                  name="kode_baki"
                  options={databaki}
                  placeholder={"Silahkan Pilih Kode Baki"}
                  component={ReanderSelect}
                />
              ) : (
                <SkeletonLoading label="Kode Baki" />
              )}
            </div>
              <div className="col-lg-9"></div>
            <div className="col-3">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Filter Laporan"
                )}
              </button>
            </div>
            <div className="col-12">
              <Tabel
                keyField="kode_barcode"
                data={this.props.data}
                columns={this.props.columns}
                empty={true}
                textEmpty="Data Laporan Barang Detail Kosong"
              />
            </div>
            {this.props.export ? (
              <>
                <div className="col-lg-6">
                  <button
                    type="button"
                    onClick={() => this.exportPdf()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export PDF{" "}
                  </button>
                </div>
                <div className="col-lg-6">
                  <LaporanDetailBarangExel
                    data={this.props.data}
                    kode_baki={this.props.kode_baki}
                  />
                </div>
              </>
            ) : null}
          </div>
          {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
        </form>
      </>
    );
  }
}

HeadFilterLaporan = reduxForm({
  form: "HeadFilterLaporan",
  enableReinitialize: true,
})(HeadFilterLaporan);
export default connect(maptostate, null)(HeadFilterLaporan);
